<template>
    <div class="arrows-box" ref="arrowsBox">
        <div class="info-box">
            <div class="arrows-title" @mousedown="move" onselectstart="return false">
                设备总数量:
                <span style="color:#FC7C3D;font-size: 1.1em;vertical-align: bottom;line-height: 1.1em;">{{DeviceTotal.total}}</span>
                <button class="list-operate" style="float: right" @click="dropDown">
                    <i class="hasIcon">
                        <icon :name="iconName.name"></icon>
                    </i>{{iconName.title}}
                </button>
            </div>
            <div class="numberContent">
                <div class="circleDiv color1" v-for="(item, index) in smokeDevice">
                    <div class="circle">
                        <span class="bigNumber" :style="{color: item.color}">
                            {{item.num}}
                             <span class="spanBorder" v-if="index != 3"></span>
                        </span>
                    </div>
                    <p class="face-header">
                        <img :src="item.icon" style="margin-top:0.25em;width:1.28em;height:1.31em;vertical-align: bottom">
                        <span :style="{color: item.color}">{{item.title}}</span>
                    </p>
                </div>
            </div>
        </div>
        <div class="list-content" style="font-size: 0.94em;height: calc(85% - 1em); display: flex; padding: 0" v-if="open">
            <div class="box-table">
                <div class="noborder-ListHeader">
                    <i class="hasIcon hasImg">
                        <img src="dist/img/widsomApp/table_list.png"/>
                    </i>
                    设备列表
                </div>
                <div ref="myOwnTableModular" class="table-container" style="height:calc(100% - 4em);">
                    <tables ref="myTableModular" class="dialogTable" :tableOpt="dialogTable" style="margin:0em;width:100%;"
                            @rowDbClick="MaxMapPoint"></tables>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import 'vue-awesome/icons/angle-down'
    import 'vue-awesome/icons/angle-up'
    export default {
        name: "modular",
        data() {
            return {
                dialogTable: {
                    column: [
                        {
                            title: '状态', width: 40, field: 'status', textAlign: 'center', type: 'string',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                switch (vals) {
                                    case '1':
                                        return `<div style="color: #2F7FC4">在线</div>`
                                    case '2':
                                        return `<div style="color: #9FA1A0">离线</div>`;
                                    case '3':
                                        return `<div>停用</div>`;
                                    case '4':
                                        return `<div style="color: #F4564D">告警</div>`;
                                    case '5':
                                        return `<div style="color: #E18137">欠压</div>`;
                                    case '6':
                                        return `<div style="color: #3967D8">启用</div>`;
                                }

                            }
                        },
                        {title: '设备编号', field: 'cno', textAlign: 'center', width: 100},
                        {
                            title: '安装位置', width: 140, field: 'installAddr', textAlign: 'center', type: "object",
                            formatter: function (val, index, row, obj) {
                                let template;
                                if (row.lat && row.lng) {
                                    template = `<span class="list-operate"  @click="openAccount" style="color:#2F7FC4" title="${val}">${val}</span>`;
                                } else {
                                    template = `<span title="${val}">${val}</span>`;
                                }
                                return {
                                    template: template,
                                    methods: {
                                        openAccount: function () {
                                            obj.$parent.changeMap(row);
                                        }
                                    }
                                }

                            }
                        },
                        {
                            title: '更新时间', width: 120, field: 'updateTime', textAlign: 'center', type: 'string',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                if (vals == '') {
                                    return '';
                                }
                                return obj.getTrueDate(parseInt(vals))
                            }
                        },
                    ],
                    noFooter: true,
                },
                open: true,
                width: '',
                h: '',
            }
        },
        computed: {
            smokeDevice: function () {
                return [
                    {color: "#3794E4", title: "在线", icon: "dist/img/face/facezs.png", num: this.DeviceTotal.online},
                    {
                        color: "#EEAF45",
                        title: "离线",
                        icon: "dist/img/smokeFeeling/outline_icon.png",
                        num: this.DeviceTotal.offline
                    },
                    {
                        color: "#EE7131",
                        title: "欠压",
                        icon: "dist/img/smokeFeeling/low_icon.png",
                        num: this.DeviceTotal.alarm
                    },
                    {
                        color: "#F25449",
                        title: "告警",
                        icon: "dist/img/face/faceyc.png",
                        num: this.DeviceTotal.undervoltage
                    },
                ];
            },
            iconName: function () {
                if(this.open){
                    return {
                        name: 'angle-up',
                        title: "收起"
                    }
                } else {
                    return {
                        name: 'angle-down',
                        title: '展开'
                    }
                }
            }
        },
        props: ['DeviceTotal', 'tableData'],

        methods: {
            move(e){
                let self = this;
                let odiv = this.$refs.arrowsBox;    //获取目标元素
                //算出鼠标相对元素的位置
                let disX = e.clientX - odiv.offsetLeft;
                let disY = e.clientY - odiv.offsetTop;

                //鼠标按下并移动的事件
                document.onmousemove = (e)=>{
                    //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
                    let left = e.clientX - disX;
                    let top = e.clientY - disY;
                    console.log(left, top);
                    if(left < 0){
                        left = 0;
                    }
                    if(top < 0){
                        top = 0
                    }
                    if(self.width - left < 530){
                        left = self.width - 530
                    }
                    if(self.h - top < odiv.offsetHeight){
                        top = self.h - odiv.offsetHeight;
                    }
                    //移动当前元素
                    odiv.style.left = left + 'px';
                    odiv.style.top = top + 'px';
                };
                document.onmouseup = (e) => {
                    document.onmousemove = null;
                    document.onmouseup = null;
                };
            },
            dropDown: function () {
                this.open = !this.open
            },
            MaxMapPoint: function (row) {
                this.$parent.MaxMapPoint(row);
            },
            changeMap: function (row) {
                this.$parent.changeMap(row);
            }
        },
        mounted: function () {
            this.$refs.myTableModular.resetTableOpt({data: this.tableData})
        },
        created: function () {
            this.h = window.screen.height;
            this.width = window.screen.width;
            let height = ((this.h*0.9 - 100) * 0.85 - 125);
            this.dialogTable.height = height;
        }
    }
</script>

<style lang="scss" scoped>
    .arrows-box {
        position: absolute;
        top: 100px;
        left: 50px;
        width: 500px;
        background: rgba(20, 20, 20, 0.8);
        padding: 15px;
        .arrows-title {
            text-align: left;
            overflow: hidden;
            border-bottom: 1px solid #888;
        }
        .numberContent {
            padding: 1em 0;
            display: flex;
            justify-content: space-around;
            .circleDiv {
                display: table-cell;
                vertical-align: middle;
                width: 25%;
                text-align: center;
                height: 6.4em;
                .circle {
                    cursor: pointer;
                    width: 100%;
                    span {
                        width: 100%;
                        display: block
                    }
                    span.bigNumber {
                        font-size: 2.8em;
                    }
                    span.spanBorder {
                        width: 0.05em;
                        background: #4D5F75;
                        display: block;
                        height: 0.8em;
                        float: right;
                        margin-top: 0.15em;
                    }
                }
                p {
                    font-size: 1.1em;
                    margin-left: 35%;
                    padding: 0;
                    width: 50%;
                    span {
                        margin-left: 0.1em
                    }
                }
                .face-header {
                    width: 4em;
                    padding-left: calc(50% - 2em);
                    margin-left: 0
                }
                .face-header:before {
                    width: 0
                }
            }
        }
    }
    .list-content{
        border-top: 1px solid #888;
    }
</style>


